.todo_container {
    max-width: 300px;
    align-items: center;
    border: 2px solid lightseagreen;
    border-radius: 4px;
    margin: 1rem auto;
    background-color: powderblue;
    min-height: 380px;
}

.todo_item {
    margin: 1rem;
    display: flex;
    justify-content: space-between;
}
